  outline: none;
  cursor: not-allowed;
  opacity: 0.3;
  pointer-events: none;
}

.custom-field__text {
  display: inline-flex;
  align-items: center;
  padding: 0 0 0 16px;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.custom-field__input-icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
}
.custom-field__input-icon--radio {
  position: relative;
  border-radius: 50%;
}
.custom-field__input-icon--checkbox {
  border-radius: 4px;
}

.custom-field__input[type=radio]:checked + .custom-field__input-icon::before {
  content: "";
  position: absolute;
  display: block;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}

.custom-field__input[type=checkbox]:checked + .custom-field__input-icon::before {
  content: "";
  position: absolute;
  width: 22px;
  height: 16px;
  background-image: url("../img/stack.svg#icon-check-mark");
